<template>
  <div class="nav">
    <div class="container-fluid noselect">
      <div
        :class="{ 'no-background': onHome }"
        class="
          white-bar
          d-flex
          justify-content-between
          align-items-center
          p-0
          m-0
        "
      >
        <div v-if="nameLoaded">
          <div
            v-if="!onHome"
            class="name other-buttons"
            @click="showMenu = false"

          >
            <router-link tag="div" class="turn-orange" to="/">
              <p class="pages-button">Roxana</p>
              <p class="pages-button">Kenjeeva</p>
            </router-link>
          </div>
          <div
            v-if="onHome"
            class="name other-buttons turn-orange"
            @click="showMenu = false"
          >
            <router-link tag="div" class="turn-orange" to="/">
              <p class="pages-button">Visual Artist</p>
              <p class="pages-button">&#38; Designer</p>
            </router-link>
          </div>
        </div>

        <div class="flex-grow-1 mx-3"></div>

        <div class="menu-button other-buttons" @click="showMenu = !showMenu">
          <MenuButton :expand-menu="showMenu" />
        </div>
      </div>
      <transition name="expand">
        <div class="menu p-1" v-if="!!showMenu" @click="showMenu = !showMenu">
          <div
            class="
              d-flex
              flex-column
              other-buttons
              justify-content-end
              flex-wrap
              mx-2
              turn-orange
            "
          >
            <router-link to="/about" tag="div" class="text-end turn-orange"
              ><p class="menu-item mb-3">About</p>
            </router-link>
            <router-link
              to="/projects"
              tag="div"
              class="text-end turn-orange"
              :class="{ 'router-link-active': onProjects }"
              ><p class="menu-item mb-3">Projects</p></router-link
            >

            <span class="text-end menu-item mb-3"
              ><a class="turn-orange" href="mailto:roxanakenjeeva@gmail.com" target="_top"
                >Contact</a
              ></span
            >
            <router-link
              to="/shop"
              tag="div"
              class="text-end turn-orange"
              :class="{ 'router-link-active': onShop }"
              ><p class="menu-item mb-3">Shop</p></router-link
            >
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import MenuButton from "@/components/ui/MenuButton.vue";
export default {
  components: {
    MenuButton,
  },

  data() {
    return {
      nameLoaded: false,
      showMenu: false,
      onProjects: false,
      onShop: false,
      onHome: false,
    };
  },
  methods: {
    subIsActive() {
      const path = this.$route.path;

      if (path.length < 2) {
        this.onHome = true;
      } else {
        this.onHome = false;
      }

      if (path.includes("/projects")) {
        this.onProjects = true;
      } else {
        this.onProjects = false;
      }
      if (path.includes("/shop")) {
        this.onShop = true;
      } else {
        this.onShop = false;
      }
      this.$nextTick(() => {
        this.nameLoaded = true;
      });
    },
  },
  watch: {
    $route() {
      this.subIsActive();
    },
  },
  beforeMount() {
    this.subIsActive();
  },
};
</script>

